#page-intro {
    background: $primary-color;  
    width: 100%;
    height: 100%;
    display: table;
    text-align: center;    
     
    div {
        display: table-cell;
        vertical-align: middle;
        white-space: nowrap;
        font-size: 4em;
        color: #fff; 
        font-weight: 200;
        -webkit-text-shadow: -1px 1px 8px #fff, 1px -1px 80px #eee;
        text-shadow: -1px 1px 8px #fff, 1px -1px 80px #eee;       
    }
    
    span {
        font-weight: 400;
    }
}
