.lv {
    padding: 0 16px;
    .lv-item {
        padding-left: 72px - 16px;
        padding-top: 8px;
        padding-bottom: 8px;
        font-size: 16px;
        font-weight: 400;
        height: 72px;
        line-height: 72px-8px-8px;
        // border-top: 1px solid rgba(#000, 0.12);    
    }       
}

.lv-avatar {
    @extend .lv;
    .lv-item {
        height: 30px;
        line-height: 30px-8px-8px;
    }    
}

