#page-detail {
    .app-bar {
        box-shadow: none;
    }
    .view-state {
        background-color: $primary-color;
        color: #fff;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.16);
    }
    .mbsc-desc {
        color: rgba($color: #fff, $alpha: 0.70);
    }   
    
    .overthrow {
        position: absolute;
        top: 206px;
        left: 0;
        right: 0;
        bottom: 0;
    } 
    
    .time.disabled {
        color: rgba(255,255,255,0.38);
    }
    
    .lv-timetable {
        padding: 0 16px;
        line-height: 22px;
        font-size: 16px;
        font-weight: 400;  
        color: rgba(0, 0, 0, 0.87); 
        .subtitle {
            padding-left: 0;
        }      
        
        .lv-item {
            position: relative;
            padding-left: 72px;
            min-height: 44px;
            margin-bottom: 12px;
            
            .begin, .end {
                position: absolute;
                font-weight: 600;                    
                width: 72px;
                text-align: center;            
                left: 0;
            }            
            .begin {
                top: 0;
            }
            .end {
                bottom: 0;
            }  
            .title {
                font-weight: 600;
            }   
            .people {
                font-size: 14px; 
                color: rgba(0, 0, 0, 0.54);             
            }   
            .female {
                color: #FF4081;
            }   
            .male {
                color: #0288D1;
            }   
        }        
    }
}