.app-bar {
    //@extend .position-absolute;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.16);  
    color: $appbar-font-color;
    background-color: $primary-color;
    height: $appbar-height-portrait;
    line-height: $appbar-height-portrait; 
    padding: 4px;
    overflow: hidden;
    
    @media #{$landscape} {
        height: $appbar-height-landscape;
        line-height: $appbar-height-landscape;
        padding: 0;
    }
        
    @media #{$tablet} {
        height: $appbar-height-tablet;
        line-height: $appbar-height-tablet;
        padding: 8px 12px;
    }
    
    .left, .right {
        height: 100%;
        line-height: $icon-height;
    }    
    
    /* Icons */
    .mbsc-ic {
        font-size: 2rem;
        display: inline-block;
        text-align: center;
        height: $icon-height;
        width: $icon-height;
        line-height: $icon-height;
    }
    
    .mbsc-ic:active {
        background-color: lighten($primary-color, 4%);
    }
        
    .title {
        padding-left: 20px;
        font-size: 21px;
        font-weight: 500;
        text-overflow: ellipsis;
        overflow: hidden;	
        white-space: nowrap;
        line-height: $icon-height;
        
        @media #{$tablet} {
            padding-left: 24px;
        }        
    }
}