#panel-menu {
	background: $panel-menu-background;
    
    .header {
        // 3:2
        height: (17em *2em)/(3em);
        background: url('../img/bvc-mank.at.jpg') no-repeat; //$panel-menu-background-header; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;        
        position: relative;
        
        .subtitle {
            position: absolute;
            left: 0;
            bottom: 0; 
            right: 16px;       
            line-height: 20px;            
            //height: 56px;
            height: 50px;
            color: #fff;
            font-size: 14px;
                        
            .club {
                font-weight: 500;
                display: block;
            }
            
            .name {
                font-weight: 400;
                display: block;
                color: rgba(255,255,255,0.7);
            }            
        }
        
        .mbsc-ic {
            position: absolute;
            top: 0;
            right: 0;
            font-size: 1.8rem;
            text-align: center;
            height: $icon-height;
            width: $icon-height;
            line-height: $icon-height;    
            color: #fff;     
        }
        
        .mbsc-ic:active {
            background-color: lighten($panel-menu-background-header, 4%);
        }          
    }
    
    .lv-menu {

        .lv-item {    
            position: relative;
            padding: 0 16px 0 72px;
            font-size: 14px;
            font-weight: 500;
            color: rgba(0,0,0,.87);
            height: 48px;
            line-height: 48px;  
            border-bottom: 1px solid #f0f0f0;               
        }
        
        .lv-item:active {
            background-color: darken($panel-menu-background, 4%);
        }
        
        .mbsc-ic {
            position: absolute;
            left: 16px;     
            font-size: 24px;
            color: rgba(0,0,0,.54);
            display: block;
            width: 16px;
        }  
                     
    }       
}
