#page-main {
    .main {
        @extend .position-absolute;
        top: 56px + 72px;
        
        .empty {        
            width: 100%;
            height: 100%;
            text-align: center;
            color: rgba(0,0,0,0.54);
            display: none;
            
            .cell {
                display: table-cell;                
                vertical-align: middle;                
            }
            .mbsc-ic {
                font-size: 150px;
                color: #c9c9c9;
            }     
        }
        
        .subtitle {
            padding-left: 0;
        }
        
        .overthrow {
            @extend .position-absolute;
        }
        
        .female {
            color: #FF4081;
        }   
        .male {
            color: #0288D1;
        }           
    }       
        
    
    .view-state {
        background-color: $primary-color;
        color: #fff;
        box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.16);
    }
        
    .mbsc-desc {
        color: rgba(255, 255, 255, 0.70);
    }
    
    &.mbsc-volleyball .mbsc-switch input:checked+.mbsc-switch-track .mbsc-switch-handle {
        background: $secondary-color;
    }
    
}