/* 1. Colors
   ========================================================================== */

$primary-color: color("light-green", "base") !default;
$primary-color-light: lighten($primary-color, 15%) !default;
$primary-color-dark: darken($primary-color, 15%) !default;

$secondary-color: color("deep-orange", "accent-3") !default;
$success-color: color("green", "base") !default;
$error-color: color("red", "base") !default;
$link-color: color("deep-orange", "accent-1") !default;

/* 9. Fonts
   ========================================================================== */

$roboto-font-path: "../fonts/roboto/" !default;


/* 11. Global
   ========================================================================== */

// Media Query Ranges
$small-screen-up: 601px !default;
$medium-screen-up: 993px !default;
$large-screen-up: 1201px !default;
$small-screen: 600px !default;
$medium-screen: 992px !default;
$large-screen: 1200px !default;

$portrait-small-tablet: 600px !default;
$landscape-small-tablet: 960px !default;


$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;

$portrait: "only screen and (orientation: portrait) and (max-width : #{$portrait-small-tablet})" !default;
$landscape: "only screen and (orientation: landscape) and (max-width : #{$landscape-small-tablet})" !default;
$tablet: "only screen and (orientation: portrait) and (min-width : #{$portrait-small-tablet}), only screen and (orientation: landscape) and (min-width : #{$landscape-small-tablet})" !default;

/* 12. Grid
   ========================================================================== */

$num-cols: 12 !default;
$gutter-width: 1.5rem !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: ($gutter-width*2)/3 !default;


/* 13. Navigation Bar
   ========================================================================== */

$appbar-height-tablet: 64px !default;
$appbar-height-portrait: 56px !default;
$appbar-height-landscape: 48px !default;
$appbar-font-color: #fff !default;
$appbar-brand-font-size: 2.1rem !default;


/* 20. Typography
   ========================================================================== */

$off-black: rgba(0, 0, 0, 0.87) !default;
// Header Styles
$h1-fontsize: 4.2rem !default;
$h2-fontsize: 3.56rem !default;
$h3-fontsize: 2.92rem !default;
$h4-fontsize: 2.28rem !default;
$h5-fontsize: 1.64rem !default;
$h6-fontsize: 1rem !default;

/* 22. Flow Text
   ========================================================================== */

$range : $large-screen - $small-screen !default;
$intervals: 20 !default;
$interval-size: $range / $intervals !default;

/* 23. Icons
   ========================================================================== */

$icon-height: 48px;

/* 24. Panels
   ========================================================================== */
$panel-menu-background: #fafafa;
$panel-menu-background-header: color("grey", "darken-2");