.ui-panel {
	position: absolute;
    left: 0;	
	top: 0;
	bottom: 0;
    right: 0;
    max-width: 280px;
	z-index: 100;
}

.ui-dismiss {
	display: none;  
	z-index: 99;
    background-color: rgba(0,0,0,0.3);    
}

/* Left Panel */
.ui-panel-left {
	-webkit-transform: translate(-280px,0);
	transform: translate(-280px,0);	
    right: 56px;	
    box-shadow: 8px 0 16px 0 rgba(0, 0, 0, 0.16);    
}

.no-csstransforms3d .ui-panel-left.ui-panel-open {
	-webkit-transform: translate(0,0);
	transform: translate(0,0);
}

/* 3D transforms support: translate3d benutzen */
.csstransforms3d .ui-panel-left {
	-webkit-transform: translate3d(-280px,0,0);
	transform: translate3d(-280px,0,0);
}

.csstransforms3d .ui-panel-left.ui-panel-open {	
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
}

/* Right Panel */
.ui-panel-right {
    left: 56px;
    box-shadow: -4px 0 4px 0 rgba(0, 0, 0, 0.16);    
	-webkit-transform: translate(280px,0);
	transform: translate(280px,0);		
}

.no-csstransforms3d .ui-panel-right.ui-panel-open {
	-webkit-transform: translate(0,0);
	transform: translate(0,0);
}

/* 3D transforms support: translate3d benutzen */
.csstransforms3d .ui-panel-right{
	-webkit-transform: translate3d(280px,0,0);
	transform: translate3d(280px,0,0);
}

.csstransforms3d .ui-panel-right.ui-panel-open {	
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
}

/* Animation */
.ui-panel-animate {
	-webkit-transition: all 300ms;
	transition: all 300ms;
}